.popup__container {
  width: 100%;
  max-width: 430px;
  background: #fff;
  box-shadow: 0 0 25px rgba(0, 0, 0, .15);
  border-radius: 10px;
  position: relative;
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 568px) {
  .popup__container {
    max-width: 282px;
  }
}